
.movies .movie {
    width:45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    justify-content: flex-start;
    align-items: space-between;
    font-weight: 300;
    padding:20px;
    border-radius: 5px;
    color: #adaeb9;
    min-width: 400px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie img {
    position: relative;
    top: -35px;
    width:100%;
    max-width: 180px;
    max-height: 200px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}
.movie .movie__title{
    margin-bottom: 5px;
    font-size: 14px;
    color: #2c2c2c;
}
.movie .movie__year,
.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0;
}
.movie .movie__genres li,
.movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
}